.search-wrapper {
    display: -webkit-flexbox;
    display: -ms-flexbox;
    display: -moz-flex;
    display: flex;
    -moz-flex-direction: row;
    -ms-flex-direction: row;
    flex-direction: row;
    background: #FAFAFA;
}

.search {
    display: -webkit-flexbox;
    display: -ms-flexbox;
    display: -moz-flex;
    display: flex;
    -moz-flex-direction: row;
    -ms-flex-direction: row;
    flex-direction: row;
    width: 100%;
    margin: 12px;
    padding: 8px;
    border: 1px solid #F5F5F5;
    -ms-flex-align: center;
        align-items: center;
    background: #FFF;
}

.search .search-icon {
    width: 24px;
    padding-right: 8px;
    cursor: pointer;
}

.search .search-input {
    position: relative;
    -moz-flex: 1;
    -ms-flex: 1;
    flex: 1 1;
    display: -webkit-flexbox;
    display: -ms-flexbox;
    display: -moz-flex;
    display: flex;
    outline: none;
}

.search .search-input::before {
    content: attr(placeholder);
    opacity: 0.5;
    color: inherit;
    cursor: text;
}


